.about_container {
  animation: fadeIn 5s backwards;
  .bg {
    position: fixed;
    top: 0;
    right: 0;
    z-index: -1;
    width: 100%;
    height: 100vh;
    object-fit: cover;
    background-image: url("../../../assets/cubes.png");

    h1 {
      text-align: center;
      font-size: 80px;
      -webkit-text-stroke: 2px white;
      color: transparent;
      margin-top: 20%;
    }
  }

  .about_hero {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #1c1024;
    margin-top: 43%;
    background-image: url("../../../assets/diagmonds.png");
    height: 680px;
    .img_div {
      width: 50%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .description_div {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: flex-start;
      width: 70%;
      height: 100%;
      .header_about {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 10px;

        h1 {
          font-size: 29px;
          color: white;
        }

        hr {
          width: 30%;
        }
      }

      .about_desc {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        padding: 20px;
        h1 {
          color: #a5a5a5;
          font-weight: 600;
          font-size: 25px;
        }

        p {
          color: #a5a5a5;
          line-height: 35px;
          font-weight: 200;
          margin-top: 18px;
          font-size: 14px;
          text-align: justify;
        }

        a {
          margin-top: 20px;
          background: transparent;
          border: 1px solid #a5a5a5;
          padding: 20px;
          color: #a5a5a5;
          cursor: pointer;
          text-decoration: none;
          box-shadow: 0px 0px 0px 0px white inset;
          transition: all 0.2s ease-in-out;
          &:hover {
            box-shadow: 178px 0px 12px 12px white inset;
            color: #1c1024;
          }
        }
      }
    }
  }

  .skills_hero {
    width: 100%;
    background-image: url("../../../assets/diagmonds.png");
    margin-top: 477px;
    padding-top: 20px;
    background-color: #1c1024;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;

    svg {
      position: sticky;
      top: 18px;
      left: 49%;
      color: white;
      cursor: pointer;
      z-index: 2;
      margin-top: 20px;
    }

    .skill_header {
      width: 100%;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      h1 {
        color: white;
        font-size: 30px;
      }
      hr {
        width: 30%;
      }
    }

    .skills {
      width: 100%;
      margin-top: 35px;
      position: relative;
      height: 742px;
      overflow: hidden;

      .soft_skills {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-wrap: wrap;
        padding: 20px;
        position: absolute;
        transition: all 2s ease-in-out;
        height: 100%;
      }

      .prgm_skills {
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
        width: 100%;
        position: absolute;
        transition: all 2s ease-in-out;
        transform: translateX(200%);
        height: 100%;

        .slick-slider {
          width: 70%;
        }
      }
    }
  }
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@media only screen and (min-width: 688px) and (max-width: 992px) {
  .about_container {
    .bg {
      h1 {
        font-size: 40px;
        margin-top: 50%;
      }
    }

    .about_hero {
      margin-top: 144%;
      flex-direction: column;
      height: 1439px;
      .img_div {
        width: 100%;
      }

      .description_div {
        width: 100%;
      }
    }
  }
}

@media only screen and (max-width: 688px) {
  .about_container {
    .bg {
      h1 {
        font-size: 33px;
        margin-top: 80%;
      }
    }

    .about_hero {
      margin-top: 232%;
      flex-direction: column;
      height: 1439px;
      .img_div {
        width: 100%;
      }

      .description_div {
        width: 100%;

        .about_desc {
          margin-top: 30px;

          h1 {
            font-size: 20px;
          }
        }

        .header_about {
          h1 {
            font-size: 22px;
          }
        }
      }
    }

    .skills_hero {
      .skill_header {
        h1 {
          font-size: 22px;
        }
      }
      .skills {
        overflow-y: scroll;
        .soft_skills {
          height: 200%;
        }
        .prgm_skills {
          overflow: hidden;
        }
      }
    }
  }
}
